require('jquery-pjax');
const NProgress = require('nprogress');
NProgress.configure({
  showSpinner: false,
  easing: 'ease-out',
  speed: 1000
});

$(document).pjax('a:not(.fancybox)', '#main', {
  scrollTo: $('.main').position().top - 60,
  fragment: '#main',
  timeout: 5000,
});

$(document).on('pjax:start', function () {
  // console.log('pjax:start');

  NProgress.start();
  $('html, body').animate({
    scrollTop: $('.main').position().top - 60
  }, 500);
  // if (window.dplayerInstances) {
  //   for (var i = 0; i < window.dplayerInstances.length; i++) {
  //     window.dplayerInstances[i].destroy();
  //   }
  // }
});

$(document).on('pjax:end', function () {
  // console.log('pjax:end');

  NProgress.done();
  require('./post-details')();
  // require('./leancloud')();
  // require('./share')();
  require('./pisces')()
  CONFIG.fancybox && NexT.utils.wrapImageWithFancyBox();

  // Bootstrap Motion.
  // CONFIG.motion.enable && NexT.motion.integrator.bootstrap();
  // NexT.motion.integrator.cursor = -1
  // CONFIG.motion.enable && NexT.motion.integrator.next();
  CONFIG.motion.enable && NexT.motion.middleWares.postList({next:function(){}});
  // gtag('set', 'location', window.location.href);
  // gtag('send', 'pageview');
});



