const Headroom = require('headroom.js');
const Ayano = require('./ayano.js')

var ayano = new Ayano({interval:58, image:'/images/ayano.png',targetTop: 450, el: 'back-to-top'})


// $('.back-to-top').affix({
//   offset: {
//     bottom: $('#footer').outerHeight()
//   }
// });
if (!NexT.utils.isMobile()) {
  // 导航栏的
  var headroom = new Headroom($('.site-nav')[0], {
    "tolerance": 5,
    "offset": 205,
    "classes": {
      "initial": "head-animated",
      "pinned": "slideDown",
      "unpinned": "slideUp"
    }
  });
  headroom.init();

  // 利用 data-scroll 属性，滚动到任意 dom 元素
  // $.scrollto = function (scrolldom, scrolltime) {
  //   $(scrolldom).click(function () {
  //     $(this).addClass("active").siblings().removeClass("active");
  //     $('html, body').animate({
  //       scrollTop: $('body').offset().top
  //     }, scrolltime);
  //     return false;
  //   });
  // };

  // 判断位置控制 返回顶部的显隐 back-to-top
  // console.log('startDowm', $(window).scrollTop() > 700);
  if ($(window).scrollTop() > 750) {
    ayano.startDowm()

    // console.log('startDowm');
  }
  $(window).scroll(function () {
    // console.log('scroll');

    if ($(window).scrollTop() > 700) {
      // console.log();
      ayano.startDowm()
      // console.log('startDowm');
      // console.log($(document).height()-$(document).scrollTop()-$(window).height()<=$('#footer').innerHeight());

      // if($(document).height()-$(document).scrollTop()-$(window).height()<=$('#footer').innerHeight()){
      //   $('.back-to-top').css({
      //     top: $('#footer').offset().top - $(document).scrollTop() - 128
      //   })
      // }else {
      //   // $('.back-to-top').css({
      //   //   top: 450
      //   // })
      // }
		} else if ($(window).scrollTop() <= 300) {
			ayano.startUp()
		}
  });

  // 启用
  // $.scrollto(".back-to-top", 800);
}




